.flatpickr-wrapper {
    position:                       relative;
    display:                        inline-block;
}

.flatpickr-month {
    background:                     $monthBackground;
    color:                          $monthForeground;
    fill:                           $monthForeground;
    @include getRem('height',$monthNavHeight);
    line-height:                    1;
    text-align:                     center;
    position:                       relative;
    user-select:                    none;
    overflow:                       hidden;
}

.flatpickr-prev-month, 
.flatpickr-next-month {
    text-decoration:                none;
    cursor:                         pointer;
    position:                       absolute;
    top:                            0px;
    line-height:                    16px;
