        width:                      1em;
        padding:                    0 4px 0 2px;
        height:                     50%;
        line-height:                50%;
        opacity:                    0;
        cursor:                     pointer;
        border:                     1px solid rgba($dayForeground, 0.05);
        box-sizing:                 border-box;
        &:hover {
            background:             rgba($invertedBg, 0.1);
        }
        &:active {
            background:             rgba($invertedBg, 0.2);
            border:                 1px solid transparent; //Barrierefreiheit
        }
        &:after {
            display:                block;
            content:                "";
            position:               absolute;
            top:                    33%;
        }
        &.arrowUp {
            top:                    0;
            border-bottom:          0;
            &:after {
                border-left:        4px solid transparent;
                border-right:       4px solid transparent;
                border-bottom:      4px solid rgba($dayForeground, 0.6);
            }
        }
        &.arrowDown {
            top:                    50%;
            &:after {
                border-left:        4px solid transparent;
                border-right:       4px solid transparent;
                border-top:         4px solid rgba($dayForeground, 0.6);
            }
        }
        svg {
            width:                  inherit;
            height:                 auto;
            path {
                fill:               rgba($monthForeground, 0.5);
            }
        }
    }
    &:hover {
        background:                 rgba($invertedBg, 0.05);
        span {
            opacity:                1;
        }
    }
}
 
.flatpickr-current-month {
    @include fontsize(16px);
    line-height:                    inherit;
    font-weight:                    300;
    color:                          inherit;
    position:                       absolute;
    width:                          75%;
    left:                           12.5%;
    padding:                        (0.22*$monthNavHeight) 0 0 0;
    line-height:                    1;
    @include getRem('height',$monthNavHeight);
    display:                        inline-block;
    text-align:                     center;
    transform:                      translate3d(0px, 0px, 0px);
    &.slideLeft,
    &.slideRight {
        display:                    none;
    }
    
    
    &.slideLeft {
        //transform: translate3d(-100%, 0px, 0px);
        animation: fpFadeOut $slideTime ease, fpSlideLeft $slideTime $bezier;
    }
    &.slideLeftNew {
        //transform: translate3d(100%, 0px, 0px);
        animation: fpFadeIn $slideTime ease, fpSlideLeftNew $slideTime $bezier;
    }
    &.slideRight {
        //transform: translate3d(100%, 0px, 0px);
        animation: fpFadeOut $slideTime ease, fpSlideRight $slideTime $bezier;
    }
    &.slideRightNew {
        //transform: translate3d(0, 0, 0px);
        animation: fpFadeIn $slideTime ease, fpSlideRightNew $slideTime $bezier;
    }
    
    
    span.cur-month {
        font-family:                    inherit;
        font-weight:                    700;
        color:                          inherit;
        display:                        inline-block;
        margin-left:                    0.5ch;
        padding:                        0;
        &:hover {
            background:                 rgba($invertedBg, 0.05);
        }
    }
    .numInputWrapper {
        width:                          6ch;
        width:                          unquote("7ch\0");
        display:                        inline-block;
        span.arrowUp:after {
            border-bottom-color:        $monthForeground;
        }
        span.arrowDown:after {
            border-top-color:           $monthForeground;
        }
    }
    input.cur-year {
        background:                     transparent;
        box-sizing:                     border-box;
        color:                          inherit;
        cursor:                         default;
        padding:                        0 0 0 0.5ch;
        margin:                         0;
        display:                        inline-block;
        font-size:                      inherit;
        font-family:                    inherit;
        font-weight:                    300;
        line-height:                    inherit;
        height:                         initial;
        border:                         0;
        border-radius:                  0;
        vertical-align:                 initial;
        &:focus {
            outline:                    0;
        }
        &[disabled], 
        &[disabled]:hover {
            font-size:                  100%;
            color:                      rgba($monthForeground, 0.5);
            background:                 transparent;
            pointer-events:             none;
        }
    }
}

.flatpickr-weekdays {
    background:                         $weekdaysBackground;
    text-align:                         center;
    overflow:                           hidden;
    width:                              100%;
    display:                            flex;
    align-items:                        center;
    @include getRem('height',$weekdaysHeight);
}

span.flatpickr-weekday {
    cursor:                             default;
    font-size:                          90%;
    background:                         $monthBackground;
    color:                              $weekdaysForeground;
    line-height:                        1;
    margin:                             0;
    text-align:                         center;
    display:                            block;
    flex:                               1;
    font-weight:                        bolder;
}

.dayContainer, 
.flatpickr-weeks {
    padding:                            1px 0 0 0;
}

.flatpickr-days {
    position:                           relative;
    overflow:                           hidden;
    display:                            flex;
    @include getRem('width',$daysWidth);
    &:focus {
        outline:                        0;
    }
}

.dayContainer {
    padding:                            0;
    outline:                            0;
    text-align:                         left;
    @include getRem('width',$daysWidth);
    @include getRem('min-width',$daysWidth);
    @include getRem('max-width',$daysWidth);
    box-sizing:                         border-box;
    display:                            inline-block;
    display:                            -ms-flexbox;
    display:                            flex;
    flex-wrap:                          wrap;
    -ms-flex-wrap:                      wrap;
    -ms-flex-pack:                      justify;
    justify-content:                    space-around;
    transform:                          translate3d(0px, 0px, 0px);
    opacity:                            1;
}



.flatpickr-calendar.animate .dayContainer {
    &.slideLeft {
        animation: fpFadeOut $slideTime $bezier, fpSlideLeft $slideTime $bezier;
    }
    &.slideLeft, &.slideLeftNew {
        transform: translate3d(-100%, 0px, 0px);
    }
    &.slideLeftNew {
        animation: fpFadeIn $slideTime $bezier, fpSlideLeft $slideTime $bezier;
    }
    &.slideRight {
        animation: fpFadeOut $slideTime $bezier, fpSlideRight $slideTime $bezier;
        transform: translate3d(100%, 0px, 0px);
    }
    &.slideRightNew {
        animation: fpFadeIn $slideTime $bezier, fpSlideRightNew $slideTime $bezier;
    }
}


.flatpickr-day {
    background:                 none;
    //border:                     1px solid transparent; 
    border:                     0px solid transparent; // Barrierefreiheit
    padding:                    1px; // Barrierefreiheit
    border-radius:              $dayBorderRadiusPx; //150px;
    box-sizing:                 border-box;
    color:                      $dayForeground;
    cursor:                     pointer;
    font-weight:                400;
    width:                      14.2857143%;
    flex-basis:                 14.2857143%;
    max-width:                  none;
    @include getRem('height', $daySize);
    @include getRem('line-height', $daySize);
    margin:                     0;
    display:                    inline-block;
    position:                   relative;
    justify-content:            center;
    text-align:                 center;
    &, 
    &.prevMonthDay, 
    &.nextMonthDay {
        &.inRange, &.today.inRange, &:hover, &:focus {
            cursor:             pointer;
            outline:            0;
            background:         $dayHoverBackground;
            //border-color:       $dayHoverBackground;
            border:             1px solid $dayHoverBackground; // Barrierefreiheit
            padding:            0px; // Barrierefreiheit
        }
    }
    &.today {
        //border-color:           $todayColor;
        //background:             $todayColor;
        font-weight:            bold;
        &:hover, &:focus {
            border-color:       $todayColor;
            background:         $todayColor;
            //color:              $color-white;
        }
    }
    &.selected, &.startRange, &.endRange {
        &, &.inRange, &:focus, &:hover, &.prevMonthDay, &.nextMonthDay {
            background:         $selectedDayBackground;
            box-shadow:         none;
            color:              $color-white;
            //border-color:       $selectedDayBackground;
            border:             1px solid $dayHoverBackground; // Barrierefreiheit
            padding:            0px; // Barrierefreiheit
        }
        &.startRange {
            border-radius:      ($dayBorderRadiusPx * 10) 0 0 ($dayBorderRadiusPx * 10);
        }
        &.endRange {
            border-radius:      0 ($dayBorderRadiusPx * 10) ($dayBorderRadiusPx * 10) 0;
        }
        &.startRange + .endRange {
            box-shadow:         (-5*$dayMargin) 0 0 $selectedDayBackground;
        }
        &.startRange.endRange {
            border-radius:      ($dayBorderRadiusPx * 10);
        }
    }
    &.inRange {
        border-radius:          0;
        box-shadow:             (-2.5*$dayMargin) 0 0 $dayHoverBackground;
    }
    &.disabled, &.disabled:hover {
        pointer-events:         none;
    }
    &.disabled, &.disabled:hover,
    &.prevMonthDay, &.nextMonthDay,
    &.notAllowed, &.notAllowed.prevMonthDay, &.notAllowed.nextMonthDay {
        color:                  rgba($dayForeground, 0.3);
        background:             transparent;
        border-color:           transparent;
        cursor:                 default;
    }
    &.week.selected {
        border-radius:          0;
        box-shadow:             (-2.5*$dayMargin) 0 0 $selectedDayBackground;
    }
}



