#{$namespace}.table--searchresult {
    @extend .table--base;
    th, td {
        border:                 1px solid $color-border;
    }
    th {
        a {
            color:              $color-text-base;
        }
    }
}

// Set this column to maximum width
#{$namespace}.table__expand {
    width:                      98%;
}