$form-namespace:                $namespace !default;

$formfieldMaxWidth:             none !default;
$formElementBorderColor:        #888888 !default;

// Global Form Rules
#{$form-namespace}.form {
    @include clearfix();
}

#{$form-namespace}fieldset {
    // Reset
    border:         0 none;
    padding:        0;
    margin-bottom:  1.5em;
    > *:last-child {
        margin-bottom: 0;
    }
}

#{$form-namespace}.fieldset {
    margin-bottom:  1.5em;
    // padding: 0; -> not here, overwrites panel--heavy padding!
    > *:last-child {
        margin-bottom: 0;
    }
}

#{$form-namespace}legend {
    font-weight:    bold;
    font-size:      $font-size-h2;
    padding:        0;
    margin:         0 0 0.75em 0;
}
